/* Обычный */
@font-face {
  font-family: 'Inter';
  src: url('https://fonts.googleapis.com/css2?family=Inter:wght@200;500&display=swap');
}
:root {
  --primary-text-color: #1e1e1e;
  --main-bg-color: #ffffff;
  --primary-color: #3369f3;
  --second-color: #ffffff;
  --main-text-color: #1e1e1e;
}
.layout {
  width: 100%;
  height: 100%;
  display: flex;
}

.error {
  margin: auto;
  display: flex;
  flex-flow: column;
}

.error_name {
  text-align: center;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 40px;
  color: var(--primary-text-color);
}

.error_comment {
  text-align: center;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--primary-text-color);
  padding-top: 20px;
  padding-bottom: 70px;
}

.text:active,
.text:hover,
.text {
  text-decoration: none;
  color: #666;
}

.block_button {
  margin: 0px auto;
  width: 280px;
  border-radius: 8px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  text-align: center;
  border: none;
  cursor: pointer;
}

.button__default {
  background: var(--second-color);
  color: var(--primary-color);
  font-size: 11px;
  margin-top: 14px;
}
